import React, { Component } from 'react';
import Moment from 'react-moment';
import { Card } from 'reactstrap';
import '@devexpress/dx-react-grid-bootstrap4/dist/dx-react-grid-bootstrap4.css';
import {GroupingState, IntegratedGrouping, IntegratedSorting, SortingState}  from '@devexpress/dx-react-grid';
import { Grid, Table, TableGroupRow, TableHeaderRow, Toolbar, GroupingPanel } from '@devexpress/dx-react-grid-bootstrap4';
import logo from './logo.svg';
import './App.css';
import {categories, groups, items} from './dataFunc'

var data = {categories, groups, items}

class App extends Component {
constructor(props){
  super(props);
  this.state = {
    columns: [
      {name: 'id', title: 'ID'},
      {name: 'group', title: 'Group'},
      {name: 'name', title: 'Name'},
      {name: 'value', title: 'Value'},
      {name: 'date_from', title: 'Date from'},
      {name: 'date_to', title: 'Date to'},
    ],
    rows: items
  };
}

componentDidMount(){
  this.setState(data);
}

  render() {
    return (
      <div className="App">
        {console.log(this.state)}
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <Card>
          <Grid rows={this.state.rows} columns={this.state.columns}>
            <SortingState defaultSorting={[
              {columnName: "group", direction: "asc"},
              {columnName: "id", direction: "asc"}]}/>
            <GroupingState grouping={[{columnName: 'group'}]} />
            <IntegratedSorting />
            <IntegratedGrouping />
            <Table />
            <TableHeaderRow showSortingControls/>
            <TableGroupRow />
            <Toolbar />
            <GroupingPanel showSortingControls />
          </Grid>
        </Card>
      </div>
    );
  }
}

export default App;



