var data = require('./db.json');
var moment = require('moment');
var categories = data.filter(item => item.id.length === 1);
var groups = data.filter(item => item.id.length === 3);
var items = data.filter(item => item.id.length === 5);

categories.map(category => category.children = groups.filter(group => group.id.startsWith(category.id)));
groups.map(group => group.children = items.filter(item => item.id.startsWith(group.id)));
groups.map(group => group.category = categories.filter(category => group.id.startsWith(category.id))[0].id);
items.map(item => item.group = groups.filter(group => item.id.startsWith(group.id))[0].id);
items.map(item => item.value = parseFloat(item.value));
items.map(item => item.date_to ? item.date_to = moment(item.date_to, 'DD-MM-YYYY').format('YYYY-MM-DD') : item.date_to = "");
items.map(item => item.date_from ? item.date_from = moment(item.date_from, 'DD-MM-YYYY').format('YYYY-MM-DD') : item.date_from = "");

export {categories};
export {groups};
export {items};



